.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-40%, 20%);
		z-index: 2;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(10%, 20%);
			@include min-md {
				width: 200px;
				transform: translate(20%, 20%);
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(20%, -30%) scaleX(-1);
		z-index: 2;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(20%, 20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
		.orn-5 {
			position: absolute;
			top: 90%;
			right: 35%;
			width: 140px;
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, 20%);
		z-index: 2;
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-7 {
			position: absolute;
			top: 0;
			right: 35%;
			width: 140px;
			transform: translate(0, -50%);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(55%, 30%) scaleX(-1);
		z-index: 2;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-9 {
			position: absolute;
			bottom: 50%;
			left: 0;
			width: 180px;
			transform: translate(30%, 0);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
				transform: translate(40%, 0);
			}
			.image-element.animate-loop {
				transform-origin: 0% 40%;
			}
			.orn-10 {
				position: absolute;
				bottom: 50%;
				right: 30%;
				width: 80px;
				transform: translate(-20%, 5%) scaleX(-1);
				@include min-md {
					width: 100px;
				}
				@include min-lg {
					width: 120px;
					transform: translate(-20%, 0) scaleX(-1);
				}
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 30%;
			height: 100%;
			margin: 0 auto;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 50%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				width: 100%;
				padding: 3rem 1rem;
				border-radius: 500px;
				background-color: rgba($color: #fff, $alpha: 0.4);
				overflow: hidden;
				text-align: center;
				.background-element {
					position: relative;
					background-image: url(../images/bg-card.webp);
					background-size: cover;
					background-position: 50% 50%;
					&::after {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						box-shadow: inset 25px 25px 50px rgba($color: #000000, $alpha: 0.1);
						border-radius: 500px;
						content: "";
					}
				}
				&-content {
					position: relative;
					padding: 3rem 2rem;
					width: fit-content;
					max-width: 100%;
					margin: 0 auto;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(80%, 0, 0);
	}
	20% {
		transform: translate3d(50%, 5%, 0);
	}
	40% {
		transform: translate3d(30%, 0, 0);
	}
	60% {
		transform: translate3d(20%, 5%, 0);
	}
	80% {
		transform: translate3d(10%, 0, 0);
	}
	100% {
		transform: translate3d(0%, 5%, 0);
	}
}

[data-anim="slider-image"].has-animate {
	animation-name: slider-image;
	// animation-duration: 5000ms !important;
}

@keyframes slider-image {
	from {
		background-position: 100% 50%;
	}
	to {
		background-position: 50% 50%;
	}
}
