.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-55%, 0);
		z-index: 2;
		pointer-events: none;
		@include min-md {
			width: 160px;
			transform: translate(-45%, 0);
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-2 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 180px;
			transform: translate(30%, -20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			.image-element.animate-loop {
				transform-origin: 0% 40%;
			}
			.orn-3 {
				position: absolute;
				top: 0;
				right: 20%;
				width: 80px;
				transform: translate(0, -35%) scaleX(-1);
				@include min-md {
					width: 100px;
					transform: translate(0, -45%) scaleX(-1);
				}
				@include min-lg {
					width: 120px;
					transform: translate(0, -50%) scaleX(-1);
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, -15%) scaleX(-1);
		z-index: 2;
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 0% 40%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		z-index: 2;
		@include min-md {
			width: 180px;
			transform: translate(-40%, 0);
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(30%, 0);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(55%, 30%) scaleX(-1);
		z-index: 2;
		@include min-md {
			width: 200px;
			transform: translate(50%, 30%) scaleX(-1);
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(-10%, -50%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			img {
				transform-origin: 0% 100%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 5rem 2rem 2rem;
			background-color: var(--color-main);
			border-radius: 500px;
			box-shadow: inset 0 15px 30px rgba($color: #000000, $alpha: 0.1);
			@include max-md {
				padding: 3rem 1rem 1rem;
			}
			.thank-body {
				position: relative;
				padding-bottom: 2rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
