.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 3rem;
		left: 0;
		width: 140px;
		transform: translate(-40%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 10% 80%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 1rem;
		right: 0;
		width: 140px;
		transform: translate(40%, 0) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-3 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 140px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 160px;
				transform: translate(-10%, 0);
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		@include min-md {
			width: 220px;
			transform: translate(-30%, 0);
		}
		@include min-lg {
			width: 240px;
		}
		.image-element.animate-loop {
			transform-origin: 0% 40%;
		}
		.orn-5 {
			position: absolute;
			bottom: 10%;
			right: 25%;
			width: 80px;
			transform: translate(-20%, -45%) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
				transform: translate(-20%, -40%) scaleX(-1);
			}
		}
	}
	.orn-6 {
		position: relative;
		width: 70px;
		margin: 0 auto 1rem;
		@include max-lg {
			width: 60px;
		}
		@include max-md {
			width: 50px;
		}
		path {
			fill: var(--color-primary);
		}
	}
	.reminder-content {
		position: relative;
		padding: 0 1rem;
		margin-top: -6.5rem;
		z-index: 2;
		@include max-md {
			margin-top: -5rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
