.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		.couple-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 500px;
		}
	}
	.couple-wrapper {
		position: relative;
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-60%, 0);
			@include min-md {
				width: 180px;
				transform: translate(-50%, 0);
			}
			@include min-lg {
				width: 200px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: translate(50%, 0) scaleX(-1);
			@include min-md {
				width: 180px;
				transform: translate(40%, 0) scaleX(-1);
			}
			@include min-lg {
				width: 200px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-40%, 0);
			@include min-md {
				width: 180px;
				transform: translate(-30%, 0);
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 0% 40%;
			}
			.orn-4 {
				position: absolute;
				top: 0;
				right: 20%;
				width: 140px;
				transform: translate(0, -40%);
				@include min-md {
					width: 160px;
				}
				@include min-lg {
					width: 180px;
				}
				img {
					transform-origin: 0% 100%;
				}
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(50%, 0) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-50%, 30%);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
		.orn-7 {
			position: absolute;
			top: 43%;
			left: 0;
			width: 180px;
			transform: translate(0, -50%);
			z-index: 2;
			@include min-md {
				top: 40%;
				width: 200px;
				transform: translate(0, -70%);
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
		.orn-8 {
			position: absolute;
			top: 55%;
			left: 0;
			width: 140px;
			transform: translate(-55%, -50%);
			z-index: 2;
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
		.orn-9 {
			position: absolute;
			bottom: 3rem;
			right: 0;
			width: 140px;
			transform: translate(40%, 0) rotate(35deg) scaleX(-1);
			z-index: 2;
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 0% 100%;
			}
		}
		.orn-10 {
			position: absolute;
			top: 43%;
			right: 0;
			width: 180px;
			transform: translate(0, -50%) scaleX(-1);
			z-index: 2;
			@include min-md {
				top: 40%;
				width: 200px;
				transform: translate(0, -70%) scaleX(-1);
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
		.orn-11 {
			position: absolute;
			top: 55%;
			right: 0;
			width: 140px;
			transform: translate(55%, -50%) scaleX(-1);
			z-index: 2;
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
		.orn-12 {
			position: absolute;
			bottom: 3rem;
			left: 0;
			width: 140px;
			transform: translate(-40%, 0) rotate(-35deg);
			z-index: 2;
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 0% 100%;
			}
		}
	}
	.couple-body {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple-content {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 3rem 2rem 2rem;
		background-color: var(--color-main);
		border-radius: 500px;
		box-shadow: inset 0 15px 30px rgba($color: #000000, $alpha: 0.1);
		overflow: hidden;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 6/7;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 2rem;
			z-index: 5;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
