.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 2rem;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		z-index: 2;
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(55%, -20%) scale(-1);
		z-index: 2;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		img {
			transform-origin: 10% 80%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 3rem;
		right: 0;
		width: 180px;
		transform: translate(10%, 0) scaleX(-1);
		z-index: 2;
		pointer-events: none;
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		.image-element.animate-loop {
			transform-origin: 0% 40%;
		}
		.orn-4 {
			position: absolute;
			bottom: 40%;
			right: 0;
			width: 80px;
			transform: translate(-40%, 0) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-55%, 0);
		z-index: 2;
		pointer-events: none;
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(30%, 0);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(50%, 0) scaleX(-1);
		z-index: 2;
		pointer-events: none;
		@include min-md {
			width: 180px;
			transform: translate(40%, 0) scaleX(-1);
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(30%, 0);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 3rem;
		border-radius: 500px;
		overflow: hidden;
		@include max-xs {
			padding: 5rem 2rem;
		}
		.background-element {
			&::after {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				box-shadow: inset 25px 25px 50px rgba($color: #000000, $alpha: 0.1);
				border-radius: 500px;
				content: "";
			}
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
