.apology-section {
	position: relative;
	padding: 5rem 0;
	box-shadow: inset 25px 25px 50px rgba($color: #000000, $alpha: 0.1);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, -15%);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 0% 40%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(50%, -10%) scaleX(-1);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-55%, 40%);
		@include min-md {
			width: 180px;
			transform: translate(-50%, 40%);
		}
		@include min-lg {
			width: 200px;
		}
		img {
			transform-origin: 10% 80%;
		}
	}
	.appology-wrap {
		position: relative;
	}
}
