.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0 10rem;
		@include max-md {
			padding: 3rem 0 8rem;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-40%, -30%);
			@include min-md {
				width: 200px;
				transform: translate(-30%, -30%);
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 0% 40%;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(40%, -40%) scaleX(-1);
			@include min-md {
				width: 160px;
				transform: translate(30%, -30%) scaleX(-1);
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 0% 100%;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-50%, 20%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(50%, 0) scale(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(10%, 0) scaleX(-1);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			.image-element.animate-loop {
				transform-origin: 0% 40%;
			}
			.orn-6 {
				position: absolute;
				bottom: 40%;
				right: 0;
				width: 80px;
				transform: translate(-40%, 0) scaleX(-1);
				@include min-md {
					width: 100px;
				}
				@include min-lg {
					width: 120px;
				}
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		z-index: 3;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
